	module.exports = {"data":{"markdownRemark":{"html":"<p>\"Le parler en langue\" est l’un des thèmes les plus controversés dans le christianisme aujourd'hui, mais c’est pourtant un sujet simple à comprendre lorsqu'il est étudié à la lumière de la Parole de Dieu.  Plusieurs théories ont été élaborées et débattues sur ce phénomène mis particulièrement en exergue dans certaines dénominations. </p>\n<p>Ce site n'a pas été réalisé par une église ou une religion mais par deux jeunes amoureux de la Bible, désireux de partager les lumières qu'ils y ont découvert sur ce sujet avec ceux qui se posent des questions.</p>\n<p>Si vous avez des questions, n'hésitez pas à nous contacter nous serions ravis d'en discuter.</p>","frontmatter":{"title":"À propos"}}},"pathContext":{"slug":"/a-propos/"}}

